<template>
  <main class="centered">
    <div class="empty" v-if="posts.length == 0 && timedOut">
      <img class="pxr" src="../assets/imgs/sad_face.png" alt="" />
      <div>No posts</div>
      <div>Nothing to show here yet.</div>
    </div>
    <div id="posts" class="centered" v-else>
      <PostCard
        v-for="(post, index) in posts"
        :key="index"
        :post="post"
      ></PostCard>
    </div>
  </main>
</template>

<script>
import PostCard from "@/components/Post.vue";
export default {
  name: "Posts",
  components: {
    PostCard,
  },
  data() {
    return {
      posts: [],
      timedOut: false
    };
  },
  async mounted() {
    setTimeout(() => {
      this.timedOut = true;
    }, 1000);
    document.title = `Posts | Gabally's corner`;
    this.posts = await this.$fetchJson(`posts/index.json`);
  }
};
</script>

<style scoped>
.pxr {
  width: 70%;
}
.empty {
  text-align: center;
}
#posts {
  flex-direction: column;
  width: 100%;
}
</style>