<template>
  <div class="post" @click="viewPost">
    <div id="main-container">
      <h2 class="posttitle">{{ post.title }}</h2>
      <div class="post-date center">{{ formatDate(post.insertion_date) }}</div>
    </div>
    <div id="tag-container">
      <div class="post-tag" ref="tag">{{ post.tag }}</div>
    </div>
  </div>
</template>

<script>
import { getColor } from "@/utils/colorGenerator.js";

export default {
  name: "PostCard",
  props: {
    post: Object,
  },
  mounted() {
    this.$refs.tag.style.backgroundColor = getColor(this.post.tag);
  },
  methods: {
    formatDate(raw) {
      let [day, month, year] = raw.split("/");
      let date = new Date(year, month, day);

      const formatter = new Intl.DateTimeFormat("en", { month: "short" });
      let local = new Date(date);
      local.setMinutes(date.getMinutes() - date.getTimezoneOffset());
      return `${local.getDate()} ${formatter.format(local)} ${local.getFullYear()}`;
    },
    viewPost() {
      this.$router.push({
        name: "ViewPost",
        params: {
          title: this.post.title
        }
      });
    }
  }
};
</script>

<style scoped>
.post {
  width: 60%;
  border-bottom: 2px solid white;
  color: white;
  cursor: pointer;
  transition: transform 0.1s ease-in-out;
  display: flex;
  font-size: 17px;
  flex-direction: column;
}
#main-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: baseline;
}
#tag-container {
  display: flex;
  justify-content: flex-end;
}
.post:hover {
  background: rgba(247, 247, 247, 0.366); 
}
.posttitle {
  font-weight: bold;
  position: relative;
  padding: 5px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  width: 62%;
}
.post-tag {
  padding: 10px;
  margin: 7px;
  color: black;
  font-weight: bold;
  border-radius: 5px;
}
@media screen and (max-width: 800px) {
  .post {
    width: 80%;
    font-size: 15px;
  }
}
.post-date {
  padding: 4px;
}
</style>
